/* Content list */

.content-list {
	margin: 0 auto;
	max-width: 570px;
	display: flex;
	flex-direction: column;
	align-items: center;
	row-gap: 40px;
	text-align: center;
}

.content-list a {
	color: var(--accent);
}

.content-list__item {
	font-size: 18px;
	line-height: 1.5;
}

.content-list__item p + p {
	margin-top: 0.5em;
}

@media (max-width: 620px) {

	/* Social */
	.social {
		column-gap: 20px;
	}

	.social__item {
		width: 28px;
	}

	/* Content List */
	.content-list {
		row-gap: 20px;
	}

	.content-list__item {
		font-size: 16px;
	}
}